<template>
    <div class="filter-page__content">
        <div class="filter-item-wrapper response">
            <div v-if="loading" class="loading">
                <div id="loader" class="text-center">
                    <i class="fa fa-spinner fa-spin"></i>
                </div>
            </div>
            <div v-else>
                <div v-if="showFlights">
                    <div class="result main-shadow-box btn_sortDeparture" v-for="(fi,index) in flights" :key="index">
                        <div class="flight-logo-outer" v-bind:id="index">
                            <div class="flight-logo">
                                <b>{{ getFlightName(fi.priceInfo.fare.validatingCarrierCode) }}</b>
                                <!--<img v-bind:src="fi.priceInfo.fare.validatingCarrierCode" v-bind:alt="'fliggh icon '+ index">-->
                            </div>
                        </div>
                        <div class="flight-detail">
                            <div v-for="(leg, legIndex) in fi.legs" :key="legIndex">
                                <div class="outbond row" v-for="(seg, segIndex) in leg.schedules" :key="segIndex">
                                    <div class="box1 col-sm-4">
                                        <span class="airport">{{seg.departureAirport}}</span>
                                        <div class="duration-full">
                                        <span class="radio">
                                            <span class="icon"><img src="/images/departure-icon.png" alt=""></span>
                                        </span>
                                            <span class="duration">{{ getTime(seg.departureDateTime)}}</span>
                                            <span class="airport">{{ getYYYYMMDD(seg.departureDateTime)}}</span>
                                        </div>
                                    </div>
                                    <div class="box2 col-sm-4">
                                        <p class="stoppage">
                                            <span>{{getDifference(seg.arrivalDateTime, seg.departureDateTime)}}</span>
                                        </p>
                                        <div class="stoppage-point-outer">
                                            <div class="stoppage-point"></div>
                                        </div>
                                    </div>
                                    <div class="box3 col-sm-4">
                                        <p class="airport">{{seg.arrivalAirport}}</p>
                                        <p class="duration">{{getTime(seg.arrivalDateTime)}}</p>
                                        <p class="airport">{{ getYYYYMMDD(seg.arrivalDateTime)}}</p>
                                    </div>
                                </div>
                                <p class="text-center">Elapsed Time: {{ getMinutesToString(leg.elapsedTime) }}</p>
                            </div>
                        </div>
                        <div class="flight-price-outer row">
                            <div class="flight-price width-return col-sm-12">
                                <p class="price">
                                    <span>{{fi.totalFare.currencyCode}}</span> {{ formatMoney(fi.totalFare.totalAmount)
                                    }}
                                </p>
                                <a class="book-now" v-on:click.stop.prevent="bookFlightNow(fi)">BOOK NOW</a>
                            </div>
                        </div>
                        <div class="flight-dropdown-panel">
                            <div class="box1 col-sm">
                                <p class="checkin-info collapsed" data-toggle="collapse"
                                   v-bind:data-target="getUniqueId('#FlightInformation',index,0)">
                                    Flight Information&nbsp;<i class="fa fa-angle-down"></i>
                                </p>
                            </div>
                            <div class="box1 col-sm">
                                <p class="checkin-info text-center" data-toggle="collapse"
                                   v-bind:data-target="getUniqueId('#BaggageInfo', index,0)">
                                    <span class="baggaes-text" style="display: none;">
                                        <span class="icon"><i class="fa fa-briefcase"></i></span><span
                                            class="text">1 PC</span>
                                    </span>
                                </p>
                            </div>
                            <div class="box1 col-sm">
                                <p class="checkin-info text-right collapsed" data-toggle="collapse"
                                   v-bind:data-target="getUniqueId('#FareRules',index,0)">
                                    Fare Details&nbsp;<i class="fa fa-angle-down"></i></p>
                            </div>
                        </div>
                        <div class="flight-dropdown-content">
                            <div class="flight-info-dropdown collapse"
                                 v-bind:id="getUniqueId('FlightInformation',index,0)">
                                <div class="flight-info-dropdown-inner col-sm-12">
                                    <div v-for="(leg, legIndexA) in fi.legs" :key="legIndexA">
                                        <div v-for="(seg, rIndex) in leg.schedules" :key="rIndex">
                                            <div class="row">
                                                <div class="box1 col-sm">
                                                    <p class="title">{{getFlightName(seg.airlineCode)}}</p>
                                                    <p>Flight Number: </p>
                                                    <p class="title2">{{ seg.flightNumber }}</p>
                                                </div>
                                                <div class="box2 col-sm">
                                                    <p class="title">{{ getTime(seg.departureDateTime) }}</p>
                                                    <p class="text1">{{ formatDate(seg.departureDateTime) }}</p>
                                                    <p class="text1">Airport: {{ seg.departureAirport }}</p>
                                                    <p class="text1">Terminal: {{ seg.departureAirportTerminal }}</p>
                                                    <p class="text1">City: {{ seg.departureCity }}</p>
                                                    <p class="text1">Country: {{ getCountryName(seg.departureCountry) }}</p>
                                                </div>
                                                <div class="box3 col-sm">
                                                    <p class="text1">{{ getMinutesToString(seg.elapsedTime) }}</p>
                                                    <span class="arrow-icon"><i class="fa fa-arrow-right"></i></span>
                                                </div>
                                                <div class="box4 col-sm">
                                                    <p class="title">{{ getTime(seg.arrivalDateTime) }}</p>
                                                    <p class="text1">{{ formatDate(seg.arrivalDateTime) }}</p>
                                                    <p class="text1">Airport: {{ seg.arrivalAirport }}</p>
                                                    <p class="text1">Terminal: {{ seg.arrivalAirportTerminal }}</p>
                                                    <p class="text1">City: {{ seg.arrivalCity }}</p>
                                                    <p class="text1">Country: {{ getCountryName(seg.arrivalCountry) }}</p>
                                                </div>
                                                <div class="box5 col-sm">
                                                    <p class="title">Baggage</p>
                                                    <p class="baggaes-text">
                                                <span class="icon">
                                                    <i class="fa fa-briefcase"></i>
                                                </span>
                                                        <span class="text">1 PC</span>
                                                    </p>
                                                </div>
                                            </div>
                                            <div class="text1">
                                                <p>&nbsp;</p>
                                                <p>Frequency: <b>{{ seg.frequency}}</b>&nbsp;|&nbsp;
                                                    Stop Count: <b>{{ seg.stopCount}}</b>&nbsp;|&nbsp;
                                                    E-Ticket: <b>{{ seg.eTicketable}}</b> &nbsp;|&nbsp;
                                                    Miles Flown: <b>{{ seg.totalMilesFlown}}</b>&nbsp;|&nbsp;
                                                    Equipment Code: <b>{{ seg.equipmentCode}}</b>&nbsp;|&nbsp;
                                                    Equipment type for first leg: <b>{{ seg.equipmentTypeForFirstLeg}}</b>&nbsp;|&nbsp;
                                                    Equipment type for last leg: <b>{{ seg.equipmentTypeForLastLeg}}</b>&nbsp;|&nbsp;
                                                </p>
                                            </div>
                                        </div>
                                        <p class="text-center">Elapsed Time: {{ getMinutesToString(leg.elapsedTime) }}</p>
                                        <hr/>
                                    </div>
                                    <div>
                                        <p>Other data</p>
                                        <p v-html="getItemData(fi.priceInfo)"></p>
                                        <p v-html="getItemData(fi.passengerInfo)"></p>
                                        <p v-html="getItemData(fi.fareComponent)"></p>
                                    </div>
                                </div>
                            </div>
                            <div class="fare-rulu-dropdown collapse" v-bind:id="getUniqueId('FareRules',index,0)">
                                <h3 class="heading">Fare Details</h3>
                                <table class="table table-condensed">
                                    <thead>
                                    <tr>
                                        <th>Fare Charges</th>
                                        <th>Amount</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td>Base Amount</td>
                                        <td>{{fi.totalFare.currencyCode}} {{formatMoney(fi.totalFare.equivFare)}}</td>
                                    </tr>
                                    <tr>
                                        <td>Tax Amount</td>
                                        <td>{{fi.totalFare.currencyCode}} {{formatMoney(fi.totalFare.totalTax)}}</td>
                                    </tr>
                                    <tr>
                                        <td>Total Fare</td>
                                        <td>{{fi.totalFare.currencyCode}} {{ formatMoney(fi.totalFare.totalAmount) }}
                                        </td>
                                    </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-else>
                    <h4 class="text-center text-uppercase">No Flight Found</h4>
                </div>
            </div>
            <div v-if="error" class="error">
                <p class="alert alert-danger">An error occurred.</p>
            </div>
        </div>
    </div>
</template>

<script>
    import * as Cookies from 'js-cookie';
    import * as DAO from '../api/apiCalls.js';
    import * as Sabre from '../api/Sabre.js';

    import {
        getYYYYMMDD,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDifference,
        ensureTwoDigits,
        formatMoney,
        getFlightObject,
        getMinutesToString,
        isLocalHost
    } from '../api/utils.js';

    export default {
        name: 'FlightDetails',
        data() {
            return {
                countries: [],
                flights: [],
                airLines: [],
                loading: true,
                error: false,
            }
        },
        computed: {
            showFlights: function () {
                if (this.flights && this.flights.length > 0) {
                    return true;
                } else {
                    return false;
                }
            }
        },
        methods: {
            getYYYYMMDD,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            getMinutesToString,
            ensureTwoDigits,
            formatMoney,
            isObject(item) {
                if ((!!item) && (item.constructor === Object)) {
                    return this.getItemData(item);
                } else if (Array.isArray(item)) {
                    return this.getItemData(item);
                } else {
                    return item;
                }
            },
            getItemData(item) {
                let queryString = Object.keys(item).map(key => `<span style="padding: 5px; margin-right:10px;"><b>${key}</b> : ${this.isObject(item[key])}</span>`).join('');
                return queryString;
            },
            toggleClass() {
                if (this.ReduceRows === "") {
                    this.ReduceRows = "show-only-five";
                } else {
                    this.ReduceRows = "";
                }
            },
            getUniqueId: function (prefix, outerIndex, innerIndex) {
                return prefix + '-' + (outerIndex + 1) + '-' + (innerIndex + 1);
            },
            dataToSend(flight) {
                return JSON.stringify(flight);
            },
            getFlightName(flightCode) {
                let match = this.airLines.filter(o => o.Code.toLowerCase() === flightCode.toLowerCase());
                if (match.length > 0) {
                    return match[0].Name;
                }
                return flightCode;
            },
            getCountryName(countryCode) {
                let match = this.countries.filter(o => o.value.toLowerCase() === countryCode.toLowerCase());
                if (match.length > 0) {
                    return match[0].displayText;
                }
                return countryCode;
            },
            refresh(d) {
                alert("alert from flight details");
            },
            bookFlightNow(flightDetail) {
                let queryString = this.$route.query;
                if (Object.keys(queryString).length === 0) {
                    return;
                }
                let queryObject = getFlightObject(queryString);
                let segmentInfo = flightDetail.flightSegments[0];
                let pnrData = {
                    "ArrivalDT": segmentInfo.arrivalDateTime,
                    "DepartureDT": segmentInfo.departureDateTime,
                    "from": queryObject.from,
                    "to": queryObject.to,
                    "AirlineCode": flightDetail.passengerInfoList[0].passengerInfo.baggageInformation[0].airlineCode,
                    "FlightNumber": segmentInfo.flightNumber
                };
                //console.log("flightDetail");
                //console.log(JSON.stringify(flightDetail));
                //console.log(flightDetail);
                //console.log(pnrData);
                Sabre.createPNR(pnrData);
                /*
                                //define in store.js
                                this.$store.commit('setSearch', queryObject);
                                this.$store.commit('setFlightDetail', flightDetail);
                                this.$router.push("/booking");*/
            },
            fetchData() {
                this.loading = true;
                let querySting = this.$route.query;
                let qp = getFlightObject(querySting);
                if (Object.keys(querySting).length === 0) {
                    this.loading = false;
                    return;
                }
                const checkDataAvailable = new Promise((resolve, reject) => {
                    if (isLocalHost) {
                        Sabre.getLocalFlightsData(qp).then(function (response) {
                            //console.log("3 flight details");
                            resolve(response);
                        });
                    } else {
                        Sabre.searchFlights(qp).then(function (response) {
                            //console.log("3 flight details");
                            resolve(response);
                        });
                    }
                });

                const haveFlights = (result) => {
                    //console.log("4 flight details");
                    this.loading = false;
                    this.error = false;
                    this.flights = result;
                    //console.log("flights data");
                    //console.log(JSON.stringify(result));
                };

                const noFlights = (result) => {
                    console.info(result);
                    this.loading = false;
                    this.error = true;
                };
                checkDataAvailable.then(haveFlights, noFlights);
            },
        },
        beforeCreate() {
            DAO.getAirlines().then(response => {
                this.airLines = response.data;
            });
            DAO.getCountries().then(response => {
                this.countries = response.data;
            });
        },
        mounted() {
            this.fetchData();
        },
        watch: {
            // call again the method if the route changes
            //'$route': 'fetchData'
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../scss/FlightDetails.scss";
</style>
